<template>
  <div>
    <div v-if="tipoPregunta === 'respuesta_corta' || tipoPregunta === 'parrafo' || tipoPregunta === 'fecha' || tipoPregunta === 'hora'">
      <b-list-group>
        <b-list-group-item
          v-for="(respuesta,index) in respuestasPregunta"
          :key="index"
        >{{ respuesta.respuesta }}</b-list-group-item>
      </b-list-group>
    </div>
    <div v-else-if="tipoPregunta === 'varias_opciones' || tipoPregunta === 'desplegable' || tipoPregunta === 'mixto_varias_opciones' || tipoPregunta === 'mixto_desplegable'">
      <!-- <pre>{{ preguntaDetalles }}</pre> -->

      <ApexDonutChart
        :series="obtenerSeries()"
        :labels="obtenerLabels()"
      />
    </div>
    <div v-else-if="tipoPregunta === 'casillas' || tipoPregunta === 'mixto_casillas'">
      <GraficoHorizontalBarChart
        :key="`${componentKey}a`"
        :data-chart="dataHorizontalBarChart"
      />
    </div>

    <div v-else-if="tipoPregunta === 'archivo'">

      <b-list-group>
        <b-list-group-item
          v-for="(respuesta,index) in respuestasPregunta"
          :key="index"
        >
          <b-button
            size="sm"
            :href="respuesta.respuesta"
            target="_blank"
          >Archivo {{ index + 1 }}
          </b-button>
        </b-list-group-item>
      </b-list-group>
    </div>

  </div>
</template>

<script>import {
  BListGroup, BListGroupItem, BButton,
} from 'bootstrap-vue'
import { $themeColors } from '@themeConfig'
// import GraficoPie from '@/views/components/graficos/GraficoPie.vue'
import GraficoHorizontalBarChart from '@/views/components/graficos/GraficoHorizontalBarChart.vue'
import ApexDonutChart from '@/views/components/graficos/apex-chart/ApexDonutChart.vue'

export default {
  components: {
    BListGroup,
    BListGroupItem,
    BButton,

    ApexDonutChart,
    GraficoHorizontalBarChart,

  },

  // mixins: [heightTransition],
  props: {
    respuestasPregunta: {
      type: Array,
      default: () => [],
    },
    tipoPregunta: {
      type: String,
      default: '',
    },
    preguntaDetalles: {
      type: Array,
      default: () => [],
    },
    datoActualizar: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      componentKey: 0,

      dataPieChart: {
        labels: [],
        datasets: [{
          label: 'My First Dataset',
          data: [],
          backgroundColor: [
            '#836AF9',
            '#ffe800',
            '#28dac6',
            '#ffe802',
            '#FDAC34',
            '#299AFF',
            '#4F5D70',
            '#2c9aff',
            '#84D0FF',
            '#EDF1F4',
            'rgba(0, 0, 0, 0.25)',
            '#666ee8',
            '#ff4961',
            '#6e6b7b',
            'rgba(200, 200, 200, 0.2)',
          ],
          hoverOffset: 4,
        }],
      },
      dataHorizontalBarChart: {
        labels: [],
        datasets: [
          {
            data: [],
            backgroundColor: $themeColors.info,
            borderColor: 'transparent',
            barThickness: 15,
          },
        ],
      },
    }
  },
  watch: {
    datoActualizar() {
      if (this.datoActualizar === 1) {
        this.forceRerender()
      }
    },
  },

  mounted() {
    // obtenemos la cant de respuestas de cada alternativa
    // if (this.tipoPregunta === 'varias_opciones' || this.tipoPregunta === 'desplegable' || this.tipoPregunta === 'mixto_varias_opciones' || this.tipoPregunta === 'mixto_desplegable') {
    //   this.preguntaDetalles.forEach(value => {
    //     const cant = this.respuestasPregunta.filter(val => value.id === parseInt(val.respuesta, 10)).length

    //     // pieChart
    //     // this.dataPieChart.datasets[0].data.push(cant)
    //     // this.dataPieChart.labels.push(`${value.respuesta} (${cant})`)
    //   })
    // }

    if (this.tipoPregunta === 'casillas' || this.tipoPregunta === 'mixto_casillas') {
      let respuestasCasillas = ['2']
      this.respuestasPregunta.forEach(value => {
        // respuestasCasillas.concat(value.respuesta.split('[~]'))
        respuestasCasillas = [...value.respuesta.split('[~]'), ...respuestasCasillas]
        // console.log('respuestasCasillas::', respuestasCasillas.concat(value.respuesta.split('[~]')))
      })

      console.log('respuestasCasillas::', respuestasCasillas)
      this.preguntaDetalles.forEach(value => {
        const cant = respuestasCasillas.filter(val => value.id === parseInt(val, 10)).length
        // polarChart
        this.dataHorizontalBarChart.datasets[0].data.push(cant)
        this.dataHorizontalBarChart.labels.push(`${value.respuesta} (${cant})`)
      })
    }
  },

  methods: {
    forceRerender() { // se esta usando este metodo debido a que los graficos se muestra solo cuando se le fuerza a renderizar nuevamente
      this.componentKey += 1
    },
    obtenerSeries() {
      const data = []
      this.preguntaDetalles.forEach(value => {
        const cant = this.respuestasPregunta.filter(val => value.id === parseInt(val.respuesta, 10)).length
        // apexDonutChart
        data.push(cant)
      })
      return data
    },
    obtenerLabels() {
      const data = []
      this.preguntaDetalles.forEach(value => {
        // const cant = this.respuestasPregunta.filter(val => value.id === parseInt(val.respuesta, 10)).length
        // apexDonutChart
        data.push(`${value.respuesta}`)
      })
      return data
    },
  },
}
</script>

<style lang="scss" scoped>
.repeater-form {
  overflow: hidden;
  transition: .35s height;
}
.classSeccion {
border-top: #048975 10px solid;
}
.herramientas{
  background-color: white;
  border-radius: 5px;
}
.cabezeraSeccion{
  background-color: #048975;
  left: -42px;
  position: relative;
  top: -52px;
  width: 150px;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 7px 20px;
  color: white;
  font-size: 12px;
}
</style>
