<template>
  <b-card>
    <vue-apex-charts
      type="donut"
      height="350"
      :options="apexChartData.donutChart.chartOptions"
      :series="apexChartData.donutChart.series"
    />
  </b-card>
</template>

<script>
import {
  BCard,
} from 'bootstrap-vue'
import VueApexCharts from 'vue-apexcharts'
import apexChartData from './apexChartData'

export default {
  components: {
    VueApexCharts,
    BCard,

  },
  props: {
    series: {
      type: Array,
      default: () => [],
    },
    labels: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      apexChartData,
    }
  },
  beforeMount() {
    this.apexChartData.donutChart.series = this.series
    this.apexChartData.donutChart.chartOptions.labels = this.labels
  },
}
</script>
